@import url('https://fonts.googleapis.com/css?family=Boogaloo|Patrick+Hand|Montserrat|Work+Sans');

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

#root {
  display: flex;
  justify-content: center;
}


.topnav {
  left: 0;
  height: 7.5%;
  width: 100%;
  position: fixed;
  top: 0;
  color: white;
  background-color: #333;
  z-index: 1;
  text-align: center;
  vertical-align: middle;
  font-family: 'Montserrat', sans-serif;
  padding: 0.5%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.5em;
}

#home a{
  text-decoration: none;
  color: white;
}

#home a:hover {
  color: lightblue; 
}

.dropdown-heading {
  display: inline-block;
  color: #f2f2f2;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 5px solid #225fc1;
  margin: 0 50px;
  cursor: pointer;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  display: none;
  z-index: 10;
  position: absolute;
  top: 100%;
  background: black;
  text-align: center;
  width: 100%;
}

.dropdown-content a {
  display: block;
  color: #f2f2f2;
  padding: 10px;
  text-align: center;
  font-size: 1em;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  border-bottom: 5px solid #225fc1;
  margin: 0;
}

.dropdown-content, #learning {
  display: none;
}

.dropdown:hover .dropdown-content {
  display: block;
  z-index: 10;
  position: absolute;
  top: 100%;
  background: black;
  text-decoration: none;
}

.topnav a:hover, .dropdown-content a:hover, .dropdown:hover, .topnav #home:hover {
  color: lightblue;
  text-decoration: none;
}

.container {
  display: flex;
  justify-content: space-around;
  font-family: 'Boogaloo', cursive;
  font-size: 2rem;
  margin-top: 7.5%;

}

.title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex: 0.4;

  
}


.display {
  border-top: solid black;
  border-left: solid black;
  border-right: solid black;
  border-radius: 17px 17px 0 0;
  flex-grow: 1;
  width: 500px;
  height: 150px;
  font-size: 3rem;
  color: white;
  text-align: right;
  padding-right: 5px;
  background-color: grey;
  overflow: hidden;

}

.display p {
  width: 98%;
  height: 60px;
  margin: 0;
  overflow: hidden;
}

.answer {
  color: orange;
}

.buttons {
  display: flex;
  font-family: 'Patrick Hand', cursive;
}

.buttons-1, .buttons-2, .buttons-3, .buttons-4, .buttons-5 {
  display: flex;
  flex-direction: column;
  flex: 1;

}

.buttons-1 div, .buttons-2 div, .buttons-3 div, .buttons-4 div, .buttons-5 div {
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: solid;
  border-top: solid;
  border-color: #1F2421;
  font-size: 2.5rem;
  background-color: gainsboro;
  height: 75px;
}

.buttons-1 div:hover, .buttons-2 div:hover, .buttons-3 div:hover, .buttons-4 div:hover, .buttons-5 div:hover {
  background-color: orange;
}

.equal-btn {
  border-radius: 0 0 17px 0;
  border-right: solid;
}

.AC-btn {
  border-radius: 0 0 0 17px;

}

.buttons-1 .AC-btn:hover {
  background-color: red;
}

.last-row {
  border-bottom: solid;
}

.last-col {
  border-right: solid;
}

.operators div:hover {
  background-color: yellow;
}



@media only screen and (max-width : 768px) {


  .topnav {
    height: 5%;
    width: 100%;
    position: fixed;
    top: 0;
    color: white;
    background-color: #333;
    z-index: 1;
    text-align: center;
    vertical-align: middle;
    font-family: 'Montserrat', sans-serif;
    padding: 0.5%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .dropdown-heading {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #f2f2f2;
    text-decoration: none;
    text-align: center;
    font-size: 13px;
    font-weight: 500;
    text-transform: uppercase;
    border-bottom: 5px solid #225fc1;
    margin: 0 30px;
    cursor: pointer;
  }

  .dropdown {
    display: inline-block;
    position: relative;
    vertical-align: text-top;
  }

  .dropdown-content {
    display: none;
    z-index: 10;
    position: absolute;
    top: 100%;
    background: black;
    text-align: center;
    text-decoration: none;
  }

  .dropdown-content a {
    display: block;
    color: #f2f2f2;
    padding: 10px;
    text-align: center;
    font-size: 13px;
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    border-bottom: 5px solid #225fc1;
    margin: 0;
  }

  .dropdown-content, #learning {
    display: none;
  }

  .dropdown:hover .dropdown-content {
    display: block;
    z-index: 10;
    position: absolute;
    top: 100%;
    background: black;
    text-decoration: none;
  }

  .topnav a:hover, .dropdown-content a:hover, .dropdown:hover, .topnav #home:hover {
    color: lightblue;
    text-decoration: none;
  }

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 10%;
    
  }

  .title {
    font-size: 1rem;
    margin-bottom: 20px;
  }

  .title p {
    margin: 1px;
  }

  .display {
    border-top: solid black;
    border-left: solid black;
    border-right: solid black;
    border-radius: 17px 17px 0 0;
    flex-grow: 1;
    width: 300px;
    height: 150px;
    font-size: 3rem;
    color: white;
    text-align: right;
    padding-right: 15px;
    background-color: grey;
    overflow: hidden;
  
  }

  .display p {
    overflow: hidden;
    width: 100%;
  }

  .buttons {
    width: 100%;
  }

  .buttons-1, .buttons-2, .buttons-3, .buttons-4, .buttons-5 {
    height: 300px;
  }

  .buttons-1 div, .buttons-2 div, .buttons-3 div, .buttons-4 div, .buttons-5 div {
    font-size: 1.5rem;
  }
}

